import isBrowser from './isBrowser';

const detectColorScheme = () => {
  if (!isBrowser()) {
    return 'light';
  }

  if (!window.matchMedia) {
    return 'light';
  }

  const { matches } = window.matchMedia('(prefers-color-scheme: dark)');
  return matches ? 'dark' : 'light';
};

export default detectColorScheme;
