import type { FC, PropsWithChildren } from 'react';
import { memo } from 'react';
import styled from 'styled-components';

interface TagProps {
  bgColor: string;
  textColor: string;
}

const TagWrapper = styled.span<TagProps>`
  margin: 0.375rem 0.375rem 0.375rem 0.125rem;
  display: inline-block;
  font-size: 0.875rem;
  line-height: 1.3125rem;
  color: ${(props) => props.textColor};
  vertical-align: middle;
`;

const TagBorder = styled.span<Pick<TagProps, 'bgColor'>>`
  position: relative;
  background-color: ${(props) => props.bgColor};
  display: inline-block;
  border-radius: ${(props) => props.theme.pixelSize}px;

  &:before, &:after {
    content: "";
    display: block;
    position: absolute;
    pointer-events: none;
  }

  &:before {
    border-left: ${(props) => props.theme.pixelSize}px solid ${(props) => props.bgColor};
    border-right: ${(props) => props.theme.pixelSize}px solid ${(props) => props.bgColor};
    left: -${(props) => props.theme.pixelSize}px;
    top: ${(props) => props.theme.pixelSize}px;
    right: -${(props) => props.theme.pixelSize}px;
    bottom: ${(props) => props.theme.pixelSize}px;
  }

  &:after {
    border-top: ${(props) => props.theme.pixelSize}px solid ${(props) => props.bgColor};
    border-bottom: ${(props) => props.theme.pixelSize}px solid ${(props) => props.bgColor};
    left: ${(props) => props.theme.pixelSize}px;
    top: -${(props) => props.theme.pixelSize}px;
    right: ${(props) => props.theme.pixelSize}px;
    bottom: -${(props) => props.theme.pixelSize}px;
  }
`;

const TagContent = styled.div`
  padding: 0 0.25rem;
`;

const TagCorner = styled.div<Pick<TagProps, 'bgColor'>>`
  display: block;
  position: absolute;
  margin: 0;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: ${(props) => props.theme.pixelSize}px solid;
  border-image: linear-gradient(${(props) => props.bgColor}, ${(props) => props.bgColor});
  pointer-events: none;
`;

const Tag: FC<PropsWithChildren<TagProps>> = ({ children, bgColor, textColor }) => (
  <TagWrapper bgColor={bgColor} textColor={textColor}>
    <TagBorder bgColor={bgColor}>
      <TagContent>
        {children}
      </TagContent>
      <TagCorner bgColor={bgColor} />
    </TagBorder>
  </TagWrapper>
);

export default memo(Tag);
