/* eslint-disable object-curly-newline */
import { memo } from 'react';
import styled from 'styled-components';
import { mediaQueries } from '../styles';

interface FlexColumnProps {
  xs?: number;
  sm?: number;
  md?: number;
  lg?: number;
}

const calcPercent = (span?: number): number | undefined => (span ? (span / 12) * 100 : undefined);

const FlexColumn = styled.div<FlexColumnProps>`
  box-sizing: border-box;
  padding-right: 0.5rem;
  padding-left: 0.5rem;
  margin-bottom: 1.5rem;
  flex: 0 1 ${({ xs }) => calcPercent(xs) || 100}%;
  max-width: ${({ xs }) => calcPercent(xs) || 100}%;

  @media ${mediaQueries.tablet} {
    flex: 0 1 ${({ sm, xs }) => calcPercent(sm) || calcPercent(xs) || 100}%;
    max-width: ${({ sm, xs }) => calcPercent(sm) || calcPercent(xs) || 100}%;
  }

  @media ${mediaQueries.desktop} {
    flex: 0 1 ${({ md, sm, xs }) => calcPercent(md) || calcPercent(sm) || calcPercent(xs) || 100}%;
    max-width: ${({ md, sm, xs }) => calcPercent(md) || calcPercent(sm) || calcPercent(xs) || 100}%;
  }

  @media ${mediaQueries.largerScreen} {
    flex: 0 1 ${({ lg, md, sm, xs }) => calcPercent(lg) || calcPercent(md) || calcPercent(sm) || calcPercent(xs) || 100}%;
    max-width: ${({ lg, md, sm, xs }) => calcPercent(lg) || calcPercent(md) || calcPercent(sm) || calcPercent(xs) || 100}%;
  }
`;

export default memo(FlexColumn);
