import type {
  FC, PropsWithChildren, ButtonHTMLAttributes,
} from 'react';
import { memo } from 'react';
import styled from 'styled-components';

type HTMLButton = ButtonHTMLAttributes<HTMLButtonElement>;

interface ButtonProps extends HTMLButton {
  className?: string;
}

const ButtonWrapper = styled.div`
  margin: 0 ${(props) => props.theme.pixelSize}px;
`;

const ButtonBorder = styled.button<HTMLButton>`
  position: relative;
  background-color: ${(props) => props.theme.cardBackgroundColor};
  border-radius: ${(props) => props.theme.pixelSize}px;
  color: ${(props) => props.theme.cardTextColor};
  display: inline-block;
  box-shadow: none;
  border: 0;
  padding: 0.375rem 0.625rem;
  margin: 0;
  white-space: nowrap;
  cursor: pointer;
  outline: none;
  font-family: inherit;
  font-size: inherit;
  text-align: center;

  &:hover {
    background-color: ${(props) => props.theme.buttonHoverBackgroundColor};
  }

  &:active, &:focus {
    outline: none;
  }

  ::-moz-focus-inner {
    border: 0;
  }

  &:disabled {
    color: ${(props) => props.theme.buttonDisabledTextBackgroundColor};
    background-color: ${(props) => props.theme.buttonDisabledBackgroundColor};

    &:hover {
      background-color: ${(props) => props.theme.buttonDisabledHoverBackgroundColor};
    }
  }

  &:before {
    border-left: ${(props) => props.theme.pixelSize}px solid ${(props) => props.theme.cardBorderColor};
    border-right: ${(props) => props.theme.pixelSize}px solid ${(props) => props.theme.cardBorderColor};
    left: -${(props) => props.theme.pixelSize}px;
    top: ${(props) => props.theme.pixelSize}px;
    right: -${(props) => props.theme.pixelSize}px;
    bottom: ${(props) => props.theme.pixelSize}px;
  }

  &:after,&:before {
    content: "";
    display: block;
    position: absolute;
  }

  &:after {
    border-top: ${(props) => props.theme.pixelSize}px solid ${(props) => props.theme.cardBorderColor};
    border-bottom: ${(props) => props.theme.pixelSize}px solid ${(props) => props.theme.cardBorderColor};
    left: ${(props) => props.theme.pixelSize}px;
    top: -${(props) => props.theme.pixelSize}px;
    right: ${(props) => props.theme.pixelSize}px;
    bottom: -${(props) => props.theme.pixelSize}px;
  }
`;

const ButtonCorner = styled.div`
  display: block;
  position: absolute;
  margin: 0;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: ${(props) => props.theme.pixelSize}px solid;
  border-image: linear-gradient(${(props) => props.theme.cardBorderColor}, ${(props) => props.theme.cardBorderColor});
  pointer-events: none;
`;

const Button: FC<PropsWithChildren<ButtonProps>> = (props) => {
  const { className, children, ...attributes } = props;

  return (
    <ButtonWrapper className={className}>
      <ButtonBorder {...attributes}>
        {children}
        <ButtonCorner />
      </ButtonBorder>
    </ButtonWrapper>
  );
};

export default memo(Button);
