import type { FC } from 'react';
import { memo } from 'react';
import styled from 'styled-components';
import { Link } from 'gatsby';
import Container from './Container';
import FlexRow from './FlexRow';
import FlexColumn from './FlexColumn';
import logo from '../assets/images/logo.png';
import { mediaQueries } from '../styles';
import routes from '../routes';
import { LocationType } from '../types';

const HeaderWrapper = styled.div`
  padding: 1.6875rem 0 2.8125rem 0;

  @media ${mediaQueries.tabletDesktop} {
    padding: 3.375rem 0;
  }
`;

const HeaderLink = styled(Link)`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: ${(props) => props.theme.mainTextColor};
  text-decoration: none;
  height: 2rem; /* IE fix */
  border-bottom: 0;

  &:hover {
    color: ${(props) => props.theme.mainTextColor};
    border-bottom: 0;
  }
`;

const Logo = styled.img`
  height: 2rem;
  min-height: 2rem; /* IE fix */
  max-width: 2rem; /* IE fix */
  margin: 0 0.3125rem 0 0;
  border: 0;

  @media ${mediaQueries.tabletDesktop} {
    margin: 0 0.3125rem 0 0;
  }
`;

const SiteName = styled.h1`
  font-size: 1.125rem;
  line-height: 1.375rem;
  margin: 0;
  white-space: nowrap;

  @media ${mediaQueries.tabletDesktop} {
    font-size: 1.375rem;
    line-height: 1.75rem;
  }

  @media ${mediaQueries.mobile} {
    display: none;
  }
`;

const SiteNav = styled.ul`
  list-style: none;
  padding: 0;
  margin: 0;

  > li {
    font-size: 1.125rem;
    line-height: 1.375rem;
    float: right;
    padding: 0 0 0 1.6875rem;

    @media ${mediaQueries.tabletDesktop} {
      font-size: 1.375rem;
      line-height: 1.75rem;
      padding: 0 0 0 2.125rem;
    }

    a {
      color: ${(props) => props.theme.mainTextColor};

      &:hover {
        color: ${(props) => props.theme.mainTextColor};
      }

      &.active {
        border-bottom: 0.125rem solid ${(props) => props.theme.mainTextColor};
      }
    }
  }
`;

const Header: FC<LocationType> = ({ location }) => {
  const { state } = location;

  return (
    <HeaderWrapper>
      <Container>
        <FlexRow justifyContent="center" alignItems="center">
          <FlexColumn xs={3} sm={6}>
            <HeaderLink to={routes.home}>
              <Logo src={logo} alt="icelam.github.io" />
              <SiteName>icelam.github.io</SiteName>
            </HeaderLink>
          </FlexColumn>

          <FlexColumn xs={9} sm={6}>
            <SiteNav>
              <li>
                <Link
                  to={routes.about}
                  activeClassName="active"
                >
                  About
                </Link>
              </li>
              <li>
                <Link
                  to={routes.home}
                  activeClassName="active"
                  partiallyActive={state?.isProject}
                >
                  Projects
                </Link>
              </li>
            </SiteNav>
          </FlexColumn>
        </FlexRow>
      </Container>
    </HeaderWrapper>
  );
};

export default memo(Header);
