import type { FC } from 'react';
import {
  useState, useEffect, useContext, memo, useMemo,
} from 'react';
import styled, { ThemeContext } from 'styled-components';
import { animateScroll as scroll } from 'react-scroll';
import { isBrowser } from '../utils';

import Button from './Button';

type BackToTopButtonProps = { show: boolean; };

const BackToTopButton = styled(Button)<BackToTopButtonProps>`
  position: fixed;
  bottom: 1.25rem;
  right: ${(props) => props.theme.pixelSize * -2}px;
  font-size: 0.875em;
  ${(props) => (!props.show ? 'display: none;' : '')}
`;

const ButtonIcon = styled.img`
  display: block;
  width: 0.875em;
  height: auto;
  margin: 0.1875rem auto 0.3125rem auto;
`;

const scrollToTop = () => {
  scroll.scrollToTop({
    duration: 300,
  });
};

const BackToTop: FC = () => {
  const { iconTop } = useContext(ThemeContext);

  const threshold = 300;
  const initShowBackToTop = useMemo(() => (
    isBrowser()
      ? (document.documentElement.scrollTop || document.body.scrollTop) > threshold
      : false
  ), []);

  const [showBackToTop, setShowBackToTop] = useState(initShowBackToTop);

  useEffect(() => {
    const onscroll = () => {
      setShowBackToTop((document.documentElement.scrollTop || document.body.scrollTop) > threshold);
    };

    window.addEventListener('scroll', onscroll, { passive: true });

    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore - passive is a valid usage stated in MDN
    return () => window.removeEventListener('scroll', onscroll, { passive: true });
  }, []);

  return (
    <BackToTopButton onClick={scrollToTop} show={showBackToTop}>
      <ButtonIcon src={iconTop} alt="Back to Top" />
      Top
    </BackToTopButton>
  );
};

export default memo(BackToTop);
