import type { FC } from 'react';
import { memo } from 'react';
import styled from 'styled-components';

interface BorderedImageProps {
  src: string;
  srcSet?: string;
  sizes?: string;
  alt: string;
  lazyLoad?: boolean;
}

const ImageWrapper = styled.div`
  margin: 1.125rem ${(props) => props.theme.pixelSize}px;
`;

const ImageBorder = styled.div`
  position: relative;
  background-color: ${(props) => props.theme.cardBackgroundColor};
  display: block;
  border-radius: ${(props) => props.theme.pixelSize * 2}px;
  color: ${(props) => props.theme.cardTextColor};
  width: 100%;

  &:before {
    border-left: ${(props) => props.theme.pixelSize}px solid ${(props) => props.theme.cardBorderColor};
    border-right: ${(props) => props.theme.pixelSize}px solid ${(props) => props.theme.cardBorderColor};
    left: -${(props) => props.theme.pixelSize}px;
    top: ${(props) => props.theme.pixelSize}px;
    right: -${(props) => props.theme.pixelSize}px;
    bottom: ${(props) => props.theme.pixelSize}px;
  }

  &:after, &:before {
    content: "";
    display: block;
    position: absolute;
    pointer-events: none;
  }

  &:after {
    border-top: ${(props) => props.theme.pixelSize}px solid ${(props) => props.theme.cardBorderColor};
    border-bottom: ${(props) => props.theme.pixelSize}px solid ${(props) => props.theme.cardBorderColor};
    left: ${(props) => props.theme.pixelSize}px;
    top: -${(props) => props.theme.pixelSize}px;
    right: ${(props) => props.theme.pixelSize}px;
    bottom: -${(props) => props.theme.pixelSize}px;
  }
`;

const Image = styled.img`
  width: 100%;
  display: block;
`;

const ImageCorner = styled.div`
  display: block;
  position: absolute;
  margin: 0;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: ${(props) => props.theme.pixelSize}px solid;
  border-image: linear-gradient(${(props) => props.theme.cardBorderColor}, ${(props) => props.theme.cardBorderColor});
  pointer-events: none;
`;

const BorderedImage: FC<BorderedImageProps> = ({
  src, srcSet, sizes, alt, lazyLoad = true,
}) => (
  <ImageWrapper>
    <ImageBorder>
      <Image src={src} srcSet={srcSet} sizes={sizes} alt={alt} loading={lazyLoad ? 'lazy' : undefined} />
      <ImageCorner />
    </ImageBorder>
  </ImageWrapper>
);

export default memo(BorderedImage);
