import type { FC, PropsWithChildren } from 'react';
import { memo } from 'react';
import styled from 'styled-components';

interface CardProps {
  image: string;
  imageAlt: string;
  imageAspectRatio?: string;
  imageLazyLoad?: boolean;
}

type CardImageFC = (props: Pick<CardProps, 'imageAspectRatio'> & React.ImgHTMLAttributes<HTMLImageElement>) => JSX.Element;

const CardWrapper = styled.div`
  margin: 0 ${(props) => props.theme.pixelSize}px;
  height: 100%;
  display: flex;
`;

const CardBorder = styled.div`
  position: relative;
  background-color: ${(props) => props.theme.cardBackgroundColor};
  display: block;
  border-radius: ${(props) => props.theme.pixelSize}px;
  color: ${(props) => props.theme.cardTextColor};
  width: 100%;

  &:before, &:after {
    content: "";
    display: block;
    position: absolute;
  }

  &:before {
    border-left: ${(props) => props.theme.pixelSize}px solid ${(props) => props.theme.cardBorderColor};
    border-right: ${(props) => props.theme.pixelSize}px solid ${(props) => props.theme.cardBorderColor};
    left: -${(props) => props.theme.pixelSize}px;
    top: ${(props) => props.theme.pixelSize}px;
    right: -${(props) => props.theme.pixelSize}px;
    bottom: ${(props) => props.theme.pixelSize}px;
    pointer-events: none;
  }

  &:after {
    border-top: ${(props) => props.theme.pixelSize}px solid ${(props) => props.theme.cardBorderColor};
    border-bottom: ${(props) => props.theme.pixelSize}px solid ${(props) => props.theme.cardBorderColor};
    left: ${(props) => props.theme.pixelSize}px;
    top: -${(props) => props.theme.pixelSize}px;
    right: ${(props) => props.theme.pixelSize}px;
    bottom: -${(props) => props.theme.pixelSize}px;
    pointer-events: none;
  }
`;

// eslint-disable-next-line jsx-a11y/alt-text
const CardImage = styled<CardImageFC>(({ imageAspectRatio, ...props }) => (<img {...props} />))`
  width: 100%;
  display: block;
  border-bottom: ${(props) => props.theme.pixelSize}px solid ${(props) => props.theme.cardBorderColor};
  ${(props) => (props ? `aspect-ratio: ${props.imageAspectRatio}` : '')}
`;

const CardContent = styled.div`
  padding: 0.625rem 0.9375rem;
`;

const CardCorner = styled.div`
  display: block;
  position: absolute;
  margin: 0;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: ${(props) => props.theme.pixelSize}px solid;
  border-image: linear-gradient(${(props) => props.theme.cardBorderColor}, ${(props) => props.theme.cardBorderColor});
  pointer-events: none;
`;

const Card: FC<PropsWithChildren<CardProps>> = ({
  image, imageAlt, imageAspectRatio, imageLazyLoad = true, children,
}) => (
  <CardWrapper>
    <CardBorder>
      <CardImage
        src={image}
        alt={imageAlt}
        loading={imageLazyLoad ? 'lazy' : undefined}
        imageAspectRatio={imageAspectRatio}
      />
      <CardContent>
        {children}
      </CardContent>
      <CardCorner />
    </CardBorder>
  </CardWrapper>
);

export default memo(Card);
