import type { ComponentType, FC } from 'react';
import { memo, useState } from 'react';
import { ThemeProvider } from 'styled-components';
import { GlobalStyle, themes } from '../styles';
import { detectColorScheme } from '../utils';

const withGlobalStyle = <T extends object>(Component: ComponentType<T>) => {
  const ComputedComponent: FC<T> = (props) => {
    const [themeTone] = useState(() => detectColorScheme());

    return (
      <ThemeProvider theme={themes[themeTone]}>
        <>
          <GlobalStyle />
          <Component {...props} />
        </>
      </ThemeProvider>
    );
  };

  return memo(ComputedComponent);
};

export default withGlobalStyle;
